@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@300;500;700&family=Jura:wght@300;500;700&family=Anek+Kannada:wdth,wght@75..125,100..800&display=swap");
/*
    font-family: 'Cairo', sans-serif;
    font-family: 'Jura', sans-serif;
    font-family: "Anek Kannada", sans-serif;
*/

:root {
  --primary: rgb(241, 241, 241);
  --column-color: #c4c4c4;
  --secondery: rgb(10, 211, 177);
  --secondery-text: rgb(16, 167, 141);
  --gray: rgb(223, 223, 223);
  --nav-and-footer: rgb(231, 231, 231);
  --nav-and-footer-text: rgb(11, 148, 125);
  --upper-footer: #d6d6d6;
  --text-color: #161616;
  --columns: 2;
  --content-height: 420px;
  --gutter-width: 12px;
  --gutter-half-width: 6px;
  --gutter-and-half-width: 18px;
  --double-edge-border-width: 24px;
  --edge-border-width: 12px;
  --edge-border-width-fallback: 12px;
}

/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/
* {
  user-select: none;
  -webkit-user-drag: none;
}
body.lock {
  overflow: hidden;
}

body:not(.lock) {
  overflow: auto;
}

/* #region Navbar */

@keyframes menu-open {
  0% {
    height: 0px;
  }
  100% {
    height: 32px;
  }
}

.navbar {
  direction: rtl;
  margin: 0px;
  position: sticky;
  top: 0;
  padding: 0px;
  z-index: 9;
  background-color: var(--nav-and-footer);
  transition: 0.3s;
}

.navbar-brand {
  margin: auto;
  padding: 0;
  width: fit-content;
  height: fit-content;
}

.nav-item * {
  font-family: "Cairo", sans-serif;
  text-align: center;
}

.nav-item *:hover {
  color: var(--secondery) !important;
}

.nav-item *:active {
  color: var(--secondery-text) !important;
}

.navbar-collapse {
  transition-duration: 700ms;
}

.navbar-collapse:not(.show) .navbar-nav {
  position: relative;
  padding: 0 50px;
  display: flex;
  align-items: center;
}
.navbar-collapse.collapsing .navbar-nav {
  padding: 20px;
  height: fit-content;
  display: block;
}
.navbar-collapse.show .navbar-nav {
  padding: 20px;
  height: fit-content;
  display: block;
}

.navbar-collapse.collapsing .nav-item {
  padding: 10px 0px;
  width: 100%;
  text-align: right;
}
.navbar-collapse.show .nav-item {
  padding: 10px 0px;
  width: 100%;
  text-align: right;
}

.navbar-collapse.collapsing .dropdown-toggle {
  padding: 0px 0px;
  text-align: right;
}
.navbar-collapse.show .dropdown-toggle {
  padding: 0px 0px;
  text-align: right;
}
.navbar-collapse:not(.show) .dropdown-toggle {
  padding: 0px;
}
.nav-item .dropdown.show .dropdown-toggle {
  color: var(--secondery-text) !important;
}
.nav-item .dropdown .dropdown-toggle {
  width: 100%;
  display: block;
  border-radius: 0px;
  box-shadow: none !important;
  border: none;
  background-color: var(--nav-and-footer) !important;
  filter: contrast(1) !important;
}

.nav-item .dropdown .dropdown-menu {
  z-index: 10;
  filter: contrast(1.1) !important;
  padding: 10px 0px;
  margin-top: 15px;
  box-shadow: 0px 0px 5px 1px var(--text-color);
  border-radius: 10px;
  border: none;
}

.nav-item .dropdown .dropdown-item {
  text-align: center;
}

.nav-item .dropdown .dropdown-item:hover {
  background: var(--nav-and-footer);
}

.nav-item .dropdown .dropdown-item:active {
  background: var(--nav-and-footer) !important;
}

.nav-item .dropdown .dropdown-divider {
  border: 1px solid rgba(14, 14, 14, 0.63);
  filter: contrast(0.5);
  width: 90%;
  margin: 5px auto;
}

/*This section is related to the navigation button*/

.navbar-toggler {
  height: 48px;
  width: 48px;
  border: none !important;
  position: absolute;
  top: 0;
  right: 15px;
}
.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
  outline: none;
  box-shadow: none;
}
/*to prevent the X from disappearing*/

.navbar-toggler:not(.collapsed) {
  display: block;
}

/*this is for the lines of nav-button*/

.navbar-toggler span {
  display: block;
  width: 17px;
  height: 1px;
  margin: auto;
  border-radius: 0.5px;
  z-index: 9;
  background: var(--text-color);
}

.navbar-toggler span:nth-child(1) {
  animation: navbar-toggler-one-rev 400ms normal forwards;
}

.navbar-toggler span:nth-child(2) {
  animation: navbar-toggler-two-rev 400ms normal forwards;
}

.navbar-toggler:not(.collapsed) span:nth-child(1) {
  animation: navbar-toggler-one 400ms normal forwards;
}

.navbar-toggler:not(.collapsed) span:nth-child(2) {
  animation: navbar-toggler-two 400ms normal forwards;
}

/* i had to use 4 different keyframes instead of just reversing the fisrt 2 otherwise the animation wouldnt play */

@keyframes navbar-toggler-one {
  0% {
    transform: translate(0, 4px) rotate(0deg);
  }
  50% {
    transform: translate(0, 1px) rotate(0deg);
  }
  100% {
    transform: translate(0, 1px) rotate(45deg);
  }
}

@keyframes navbar-toggler-two {
  0% {
    transform: translate(0, -4px) rotate(0deg);
  }
  50% {
    transform: translate(0, 0) rotate(0deg);
  }
  100% {
    transform: translate(0, 0) rotate(-45deg);
  }
}

@keyframes navbar-toggler-one-rev {
  0% {
    transform: translate(0, 1px) rotate(45deg);
  }
  50% {
    transform: translate(0, 1px) rotate(0deg);
  }
  100% {
    transform: translate(0, 4px) rotate(0deg);
  }
}

@keyframes navbar-toggler-two-rev {
  0% {
    transform: translate(0, 0) rotate(-45deg);
  }
  50% {
    transform: translate(0, 0) rotate(0deg);
  }
  100% {
    transform: translate(0, -4px) rotate(0deg);
  }
  /* #endregion Navbar */
}

.privacypolicy {
  margin: 40px;
  text-align: left;
}

.about{
  font-family: 'Cairo', sans-serif;
  text-align: justify;
  direction: rtl;
  margin: 40px;
}

/*--------------------------------------------------------------
# social
--------------------------------------------------------------*/

.social {
  margin-bottom: 10px;
  font-size: 16px;
  color: var(--text-color);
}
.social .social-links{
  display: flex;
  justify-content: right;
}
.social .social-links a {
  font-size: 36px;
  align-items: center;
  justify-content: center;
  display: flex;
  color: var(--text-color);
  background-color: #e4e4e4;
  border-radius:25%;
  width: 55px;
  height: 55px;
  margin-right: 4px;
  transition: 0.3s;
}
.social .social-links a .fa-instagram{
  color: #f50035;
}
.social .social-links a .fa-envelope{
  color: #ffffff;
}
.social .social-links a .fa-whatsapp{
  color: #08ad4d;
}
.social .social-links a .fa-phone{
  color: #a1a1a1;
  font-size: 28px;
}
.social .social-links a:hover {
  background-color: rgb(10, 211, 177);
}
.social-links a:hover .fa-phone, .social-links a:hover .fa-whatsapp, .social-links a:hover .fa-instagram{
  color: #ffffff;
}