@media only screen and (max-width: 720px) {
  :root {
    --columns: 1 !important;
  }
}

@media only screen and (min-width: 720px) {
  :root {
    --columns: 2 !important;
  }
}

.Home .wrapper {
  display: grid;
  grid-column: auto;
  grid-gap: var(--gutter-width);
  grid-column-gap: 0;
  grid-template-columns: repeat(var(--columns), 1fr);
  padding-bottom: 85px;
}

.Home .inputForm {
  display: grid;
  grid-template-columns: 70% 30%;
  align-items: center;
  align-self: center;
  margin: 20px;
}

.Home input.dollarinput {
  margin: 0 5px;
  right: 0;
  transition: 0.3s;
  text-align: center;
  font-family: "Cairo", sans-serif;
  border-radius: 0px 10px 10px 0px !important;
}

.Home input.searchinput {
  margin: 0 5px;
  transition: 0.3s;
  text-align: center;
  position: relative;
  font-family: "Cairo", sans-serif;
  border-radius: 0px 10px 10px 0px !important;
}

.Home .input-group-text {
  border-radius: 10px 0px 0px 10px !important;
}

.Home .controlpanel {
  transition: 0.3s;
  position: fixed;
  width: 100%;
  bottom: 0;
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: var(--nav-and-footer);
  color: var(--primary-text);
  font-family: "Jura", sans-serif;
}

.Home .controlpanel .total {
  align-items: center;
  display: flex;
  justify-content: center;
}

.Home .controlpanel .pv {
  max-width: 90px;
  margin: 0 2px;
  font-size: small;
  font-weight: 600;
}

.Home .controlpanel .price {
  max-width: 120px;
  margin: 0 2px;
  font-size: small;
  font-weight: 600;
}
.Home .resetPage {
  margin: 0 3px;
  max-width: 80px;
  height: 35px;
  background-color: transparent;
  color: #e94e4e;
  box-shadow: 0 0 0px 1px inset #e94e4e !important;
  border: none;
  font-weight: 700;
  text-align: center;
  font-size: medium;
  font-family: "Cairo", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Home .resetPage:hover {
  background-color: #e96b6b;
  color: white;
}
.Home .resetPage:active {
  background-color: #e94e4e !important;
  color: white;
}
.Home .nextPage {
  margin: 0 3px;
  max-width: 80px;
  height: 35px;
  background-color: rgb(24, 192, 164);
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  text-align: center;  
  font-size: medium;
  font-family: "Cairo", sans-serif;
  color: white;
}

.Home .nextPage:hover {
  background-color: #52a188;
  color: white;
}

.Home .nextPage:active {
  background-color: #3f856f !important;
  color: white;
}
.Home .btntext{
  height: 30px;
  margin-right: 3px;
}

/* #region Copyright*/
.Home .copyright {
  position: relative;
  transition: 0.3s;
  margin: 5px 0 0 0;
  color: var(--primary-text);
  text-align: center;
  font-size: 12px;
  font-family: "Jura", sans-serif;
}

.Home .copyright span,
a {
  color: var(--nav-and-footer-text);
  font-weight: 600;
  text-decoration: none;
}
.Home .copyright a:hover {
  color: #086669;
  transition: 0.3s;
}
.Home #expand_product{
  display: block;
  width: 150vw;
  height: 150vh;
  top: 0;
  background-color: transparent;
  position: fixed;
  z-index: 9;
}
.Home #expand_product .fullimage{
  display: block;
  box-shadow: 0 0 10px 3px #3535357a !important;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: white;
  background-blend-mode: multiply;
  border: 1px solid rgb(197, 197, 197) ;
  border-radius: 15px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 90%;
  height: 50%;
  z-index: 10;
}

#expand_product .closeBtn{
 position: absolute;
 top: 5px;
 right: 5px;
 background-color: rgb(233, 233, 233);
 font-size: x-large;
 border: unset;
 width: 35px;
 height: 35px;
 display: flex;
 justify-content: center;
 align-items: center;
 align-self: center;
 color: #888888fd;
 border-radius: 10px;
}

#expand_product .closeBtn:hover{
  color: #c9c9c9;
 }

/* #endregion */
