.product {
  padding: 8px;
  margin: 2px 10px;
  border-radius: 35px;
  border: 1px solid var(--gray);
  display: grid;
  grid-auto-columns: 40% auto 55px;
  grid-auto-flow: column;
  position: relative;
  height: 100%;
}

.product .info {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-self: flex-start;
  align-items: flex-end;
  margin-left: 10px;
}

.product .name_ar {
  user-select: all !important;
  min-height: 55px;
  font-family: "Cairo", sans-serif;
  font-size: 16px;
  font-weight: 700;
  direction: rtl;
}

.product .pContainer {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-self: flex-end;
  align-items: center;
  margin-top: 5px;
}

.product .price {
  font-family: "Anek Kannada", sans-serif;
  font-size: 16px;
  font-weight: 600;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #009987;
}

.product .currencyTag {
  font-size: 14px;
  margin-right: 2px;
}

.product .seperator {
  border-radius: 1px 1px 1px 1px;
  margin: 0 5px;
  width: 1.5px !important;
  height: 20px;
  background-color: #b9b9b9;  
  align-self: flex-start;
}

.product .pv {
  font-family: "Anek Kannada", sans-serif;
  align-items: center;
  color: #b13fbb;
  display: grid;
  font-size: 14px;
  font-weight: 700;
}

.product .available {
  color: #ED8043;
  font-family: "Cairo", sans-serif;
  align-items: center;
  font-size: 14px;
  font-weight: 700;
  margin-top: 5px;
}

.product .countContainer {
  transition: height 500ms;
  border-radius: 15px;
  margin: 0 0 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-self: center;
  width: fit-content;
  background-color: #f1f1f1;
  box-shadow: inset 0px 0px 2px 1px rgb(179, 179, 179) !important;
}

.product .add {
  border: 1px solid rgb(201, 201, 201);
  font-size: small;
  width: 40px;
  height: 40px;
  box-shadow: 0px 0px 3px rgb(179, 179, 179) !important;
  border-radius: 15px;
  background-color: white;
  transition-duration: 0.2s;
}

.product .add:active {
  box-shadow: inset 0px 0px 3px rgb(179, 179, 179) !important;
  background: initial;
}

.product .count {
  height: auto;
  font-family: "Jura", sans-serif;
  text-align: center;
  font-weight: 700;
  max-width: 40px;
  font-size: small;
  border: 0;
  background: transparent;
}

.product .remove {
  border: 1px solid rgb(201, 201, 201) !important;
  box-shadow: 0px 0px 3px rgb(179, 179, 179) !important;
  font-size: small;
  width: 40px;
  height: 40px;
  border-radius: 15px;
  background-color: white;
  transition-duration: 0.2s;
}

.product .remove:active {
  box-shadow: inset 0px 0px 3px rgb(179, 179, 179) !important;
  background: initial;
}

.product .image {
  transition-duration: 0.5s;
  min-height: 120px;
  background-position-x: center;
  background-position-y: center;
  background-size: contain;
  background-color: white;
  background-repeat: no-repeat;
  background-blend-mode: multiply;
  box-shadow: rgb(212, 212, 212) 0px 0px 0.5px 1px inset;
  border-radius: 25px 25px;
}

.product:not(.added) .count {
  height: 0 !important;
  margin: 0;
  padding: 0;
}

.product:not(.added) .remove {
  height: 0 !important;
  margin: 0;
  padding: 0;
  display: none;
}

.product.added .countContainer {
  height: auto;
  align-self: center;
}

.product .IsSelectedBubble{
position: absolute;
width: 30px;
height: 30px;
border-radius: 100%;
transition-duration: 0.3s;
background-color: transparent;
text-align: center;
color: transparent;
display: flex;
justify-content: center;
align-self: flex-start;
align-items: flex-end;
font-family: "Anek Kannada", sans-serif;
font-size: 16px;
font-weight: 900;
top: 20px;
left: 20px;
}
.product.added .IsSelectedBubble{
  background-color: rgb(10, 211, 177);
  text-shadow:  rgb(124, 124, 124) 0px 0px 2px;
  box-shadow: rgb(224, 224, 224) 0px 0px 3px 0px;
  color:rgb(255, 255, 255);
} 

.product-skeleton{
  padding: 8px;
  margin: 2px 10px;
  border-radius: 35px;
  border: 1px solid var(--gray);
  display: grid;
  grid-auto-columns: 40% auto 55px;
  grid-auto-flow: column;
  position: relative;
  height: 100%;
}
.product-skeleton .image {
  margin: 0px 5px;
}
.product-skeleton .text {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly !important;
  align-items: stretch;
  width: 100%;
}
.product-skeleton .add{
  align-self: center;
  margin-left: 10px;
}