.Checkout #ordersgrid {
  margin: auto;
  width: 98% !important;
  height: 50vh !important;
}

.Checkout #ordersgrid .ag-header-cell-label {
  justify-content: center !important;
}

.Checkout .inputFormCheckout {
  display: flex;
  flex-direction: row;
  margin: 5px 3px;
  justify-content: center;
}

.Checkout .inputFormCheckout .form-control {
  font-family: "Cairo", sans-serif;
  margin: 3px 0;
  font-size: small;
}
.Checkout .inputFormCheckout .row {
  margin: 0px;
  justify-content: center;
}
.Checkout .inputFormCheckout .input-group-text {
  margin: 3px 0px;
  font-size: small;
}

/* Chrome, Safari, Edge, Opera */
.Checkout .inputFormCheckout input::-webkit-outer-spin-button,
.Checkout .inputFormCheckout input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.Checkout .input-group-text {
  border-radius: 10px 0px 0px 10px !important;
}

.Checkout .controlpanel {
  transition: 0.3s;
  position: fixed;
  width: 100%;
  bottom: 0;
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: var(--nav-and-footer);
  color: var(--primary-text);
  font-family: "Jura", sans-serif;
}

.Checkout .controlpanel .total {
  align-items: center;
  display: flex;
  justify-content: center;
}

.Checkout .controlpanel .pv {
  max-width: 90px;
  margin: 0 2px;
  font-size: small;
  font-weight: 600;
}

.Checkout .controlpanel .price {
  max-width: 120px;
  margin: 0 2px;
  font-size: small;
  font-weight: 600;
}

.Checkout .goback {
  margin: 0 3px;
  z-index: 10;
  max-width: 80px;
  background-color: transparent;
  box-shadow: 0 0 0px 1px inset #636363 !important;
  border: none;
  height: 35px;
  font-weight: 700;
  text-align: center;
  font-size: medium;
  font-family: "Cairo", sans-serif;
  color: #636363;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.Checkout .btntext{
  height: 30px;
  margin-right: 3px;
}

.Checkout .goback:hover {
  background-color: #e6abab;
  color: white;
}
.Checkout .goback:focus {
  background-color: #c59d9d !important;
  color: white;
}
.Checkout .generateText {
  margin: 0 3px;
  max-width: 80px;
  height: 35px;
  background-color: #636363;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  text-align: center;
  font-size: medium;
  font-family: "Cairo", sans-serif;
  color: white;
}

.Checkout .generateText:hover {
  background-color: #52a188;
  color: white;
}

.Checkout .generateText:focus {
  background-color: #3f856f !important;
  color: white;
}

/* #region Copyright*/
.Checkout .copyright {
  position: relative;
  transition: 0.3s;
  margin: 5px 0 0 0;
  color: var(--primary-text);
  text-align: center;
  font-size: 12px;
  font-family: "Jura", sans-serif;
}

.Checkout .copyright span,
a {
  color: var(--nav-and-footer-text);
  font-weight: 600;
  text-decoration: none;
}
.Checkout .copyright a:hover {
  color: #086669;
  transition: 0.3s;
}

/* #endregion */
